.Privacy-Policy {
    display: flex;
    flex: 1;
    flex-direction: column;
    text-align: justify;
    color: #0B0B0D;
    background-color: #D0D7D9;
}

.Privacy-Policy div {
    padding-left: 3%;
    padding-right: 3%;
    display: flex;
    flex-direction: column;
    align-self: center;
}
.Privacy-Policy div h2{
    text-align: center;
}