.Card {
    /* border: 3px solid; */
    border-radius: 10px;
    overflow: hidden;
    margin: 15px;
    display: flex;
    flex-direction: column;
    max-width: 50vh;
    min-width: 50vh;

    /* padding: 1rem; */
    /* margin: 1rem; */
    /* border-radius: 1rem; */
    /* border:#fff; */
    /* background: linear-gradient(90deg, #00dffc 0%, #05444d 100%); */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    /* color: #fff; */
    /* text-transform: uppercase; */
    /* width: 10.5rem; */
    /* height: 4rem; */
    transition: all 700ms ease-in-out;
    /* transition: cubic-bezier(0.77, 0.4, 0.175, 1) 700ms; */

}

.Card:hover {
    scale: 1.1;
    cursor: pointer;
    /* transition: all 500ms ease-out; */
    /* animation-name: unset; */
    /* animation-duration: 1s; */
    /* animation-delay: 2s; */
}

.Card .Title {
    padding: 5px 20px;
    display: flex;
    justify-content: center;
    font-weight: bold;
    color: #D0D7D9;
}


.Card .Content {
    flex-grow: 1;
    background-color: #D0D7D9;
    /* color: #0B0B0D; */
    /* padding: 10px; */

}

.Card .Content .RoletaMilionaria {
    
    padding: 10%;
    padding-top: 15%;
    flex-grow: 1;
    background-image: url("../../Images/ppRoleta.svg");
    background-size: 200%;
    background-repeat: no-repeat;
    /* background-size: cover; */
    background-position: center;
    /* color: #0B0B0D; */
    /* padding: 10px; */
    transition: all 1300ms ease-in-out;
    z-index: 10;


}

.Card .Content .RoletaMilionaria:hover {
    scale: 1.1;
    background-size: 130%;
    z-index: 10;
}

.Card .Content .MetodoNinja {
    padding: 10%;
    flex-grow: 1;
    background-image: url("../../Images/bgNinjaBet.svg");
    background-size: 100%;
    background-repeat: no-repeat;
    /* background-size: cover; */
    background-position: center;
    /* color: #0B0B0D; */
    /* padding: 10px; */
    transition: all 1300ms ease-in-out;
    z-index: 10;


}

.Card .Content .MetodoNinja:hover {
    scale: 1.1;
    background-size: 130%;
    z-index: 10;
}