.Menu {
    display: flex;
    /* flex-basis: 250px; */
    flex-direction: column;

    /* background-color: #021373; */
    /* margin-right: 1%;
    margin-left: 1%; */
    /* border-bottom-right-radius: 2ch;
    border-bottom-left-radius: 2ch; */

}
.Menu div{
    /* padding-bottom: 10px; */
    /* background-color: red; */
    border-style: solid;
    border-color: #021373;
    border-width: 0px;
    padding-bottom: 1rem;
    border-bottom-width: 1px;
    font-size:2rex;

}
.Menu nav {
    /* display:flex; */
    flex-grow: 1;
    margin-left: 2%;
    margin-right: 2%;
}

.Menu ul {
    display: flex;
    flex-wrap:wrap;
    list-style: none;
    padding: 0;
}

.Menu li {
    display: contents;
    flex: 1;
    /* justify-content:space-around; */
    /* align-items: center; */
}

.Menu li a {
    box-shadow: 0 10px 10px -10px #021373;
    flex: 1;
    padding: 10px;
    align-content: center;
    /* margin-bottom: 10px; */

    font-weight: bold;
    text-decoration: none;
    color: #D0D7D9;
    font-size: 2rex;
    /* background-color: #038C65DD; */
    /* border: 2px solid #fff; */
    border-radius: 10px;
    /* padding-left: 5%;
    padding-right: 5%; */


}

.Menu li a:hover {
    /* border-top: solid 2px #044BD9; */
    background-color: #044BD9;
    scale: 1.05;
    transition: all 700ms ease-in-out;

}